import Vue from 'vue'
import App from './App.vue'

Vue.config.productionTip = false

import VueRouter from 'vue-router'
Vue.use(VueRouter)

import Vuex from 'vuex'
Vue.use(Vuex)

// 1.  () 
//  import 
import Home from "./container/Home.vue";
import Search from "./container/Search.vue";
import Sign from "./container/Sign.vue";
import Detail from "./container/Detail.vue";
// 2. 
//  "component" 
//  Vue.extend() 
// 
// 

// 
import Homechannel from "./container/Homechannel.vue";
import DetailChannel from "./container/DetailChannel.vue";
const routes = [{
        path: '/home',
        name: 'home',
        component: Home,

        children: [{
                //  /user/:id/profile 
                // UserProfile  User  <router-view> 
                path: 'hot',
                name: 'hot',
                component: Homechannel,
            },
            {
                //  /user/:id/posts 
                // UserPosts  User  <router-view> 
                path: 'fresh',
                name: 'fresh',
                component: Homechannel,
            }
        ]

    },
    { path: '/search', name: 'search', component: Search },
    { path: '/sign', name: 'sign', component: Sign },
    {
        path: '/detail',
        name: 'detail',
        component: Detail,
        children: [{
                //  /user/:id/profile 
                // UserProfile  User  <router-view> 
                path: 'repost',
                name: 'repost',
                component: DetailChannel,
            },
            {
                //  /user/:id/posts 
                // UserPosts  User  <router-view> 
                path: 'comment',
                name: 'comment',
                component: DetailChannel,
            },
            {
                //  /user/:id/posts 
                // UserPosts  User  <router-view> 
                path: 'like',
                name: 'like',
                component: DetailChannel
            }
        ]
    },

    { path: '/', redirect: { name: 'hot' } }

]
// 3.  router  `routes` 
// , 
const router = new VueRouter({
    routes // ()  routes: routes

})

// =======vuex


const store = new Vuex.Store({
    // ======
    state: {
        gallery: {
            isShowGallery: false,
            galleryImg: [],
            index: 0
        },
        navs: [{
            title: "",
            path: "hot",
        }, {
            title: "",
            path: "fresh",
        }, {
            title: "",
            path: "",
        }, {
            title: "",
            path: "",
        }, {
            title: "",
            path: "",
        }, {
            title: "",
            path: "",
        }, {
            title: "",
            path: "",
        }, {
            title: "",
            path: "",
        }, {
            title: "",
            path: "",
        }, {
            title: "",
            path: "",
        }, {
            title: "",
            path: "",
        },{
            title: "11",
            path: "",
        }]
    },
    // ======
    mutations: {
        editGallery(state, data) {
            state.gallery = data;
        }
    },
    // ===actios  @xxx===
    actions: {
        setGallery(conText, data) {
            conText.commit('editGallery', data);
        }
    },
    // ===store()computed===

    getters: {
        getGallery: state => {
            return state.gallery
        },
        getNavs: state => {
            return state.navs
        }
    }

})












new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app')